import React, { Component } from 'react';
import { Form, Input, Button,Modal } from 'antd';
import axios from 'axios';


class Application extends Component{
    success=()=> {
        Modal.success({
            title: '新增成功！',
            content: '跳转到列表...',
        });
        this.props.history.push('/AdList')
    };

    init=()=>{
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log(values);
                axios.post(`http://cmovie.holyzq.com/api/admin/v1/advertisements`,values).then(res=>{
                    console.log(res)
                    this.success()
                })
            }
        });

    }

    handleSubmit = e => {
        e.preventDefault();
        this.init();
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                <Form.Item label="名称">
                    {getFieldDecorator('name', {
                        rules: [{ required: true, message: '请输入正确的名称' }],
                    })(<Input
                    placeholder='请输入名称'
                    />)}
                </Form.Item>
                <Form.Item label="排序">
                    {getFieldDecorator('sort', {
                        rules: [{ required: true, message: '请输入正确的名称' }],
                    })(<Input
                        placeholder='请输入排序'
                    />)}
                </Form.Item>
                <Form.Item label="广告序号">
                    {getFieldDecorator('advertisement_node_id', {
                        rules: [{ required: true, message: '请输入正确的名称' }],
                    })(<Input
                        placeholder='请输入序号'
                    />)}
                </Form.Item>
                <Form.Item label="照骗id">
                    {getFieldDecorator('photo_id', {
                        rules: [{ required: true, message: '请输入正确的名称' }],
                    })(<Input
                        placeholder='照骗标题'
                    />)}
                </Form.Item>
                <Form.Item label="广告网址">
                    {getFieldDecorator('url', {
                        rules: [{ required: true, message: 'Please input your note!' }],
                    })(<Input
                        placeholder='请输入网址'
                    />)}
                </Form.Item>

                <Form.Item wrapperCol={{ span: 12, offset: 5 }}>
                    <Button type="primary" htmlType="submit">
                        Submit
                    </Button>
                </Form.Item>
            </Form>
        );
    }
}

const CreateAdNode = Form.create({ name: 'coordinated' })(Application);

export default CreateAdNode;
