import React, {Component} from 'react';
import { Table, Divider,Modal,Form,Input,message } from 'antd';
import { Button } from 'antd';
import { Link } from "react-router-dom";
import axios from 'axios';
import './Home.css'

const {confirm} = Modal;
class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id:null,
            ad:[],
            visible:false,
            put_ok:false,
            dataNews:{
                name:'',
                sort:'',
                advertisement_node_id:'',
                photo_id:'',
                url:''
            },
        };
    }
    componentDidMount() {
        this.init()
    }
    init(){
        axios.get("http://cmovie.holyzq.com/api/admin/v1/advertisements").then(res=>{
            // console.log(res);
            this.setState({
                ad:res.data.data
            })
        })

    }

    showModal = e => {
        console.log(e)
        if(e.id){
            axios.get(`http://cmovie.holyzq.com/api/admin/v1/advertisements/${e.id}/edit`).then(res=>{
                this.setState({
                    dataNews:res.data.data.advertisement,
                    put_ok:true,
                    id:e.id
                })
            })
        }else {
            this.setState({
                dataNews:'',
                put_ok:false,
                id:null
            })
        }
        this.setState({
            visible: true,
        });
    };
    handleSubmit = e => {
        e.preventDefault();
        if(this.state.put_ok){
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    if(this.state.dataNews.name===values.name
                    &&this.state.dataNews.sort===values.sort
                    &&this.state.dataNews.advertisement_node_id===values.advertisement_node_id
                    &&this.state.dataNews.photo_id===values.photo_id
                    &&this.state.dataNews.url===values.url){
                        message.warning('请确认是否修改');
                        this.props.form.resetFields();
                    }else{
                        axios.put(`http://cmovie.holyzq.com/api/admin/v1/advertisements/${this.state.id}`,values).then(res=>{
                            Modal.success({
                                title: '修改成功！',
                                content: '回到列表...',
                            });
                            this.setState({
                                visible: false,
                            });
                            this.init();
                            this.props.form.resetFields()
                        })
                    }

                }
            });
        }else{
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    axios.post(`http://cmovie.holyzq.com/api/admin/v1/advertisements`,values).then(res=>{
                        Modal.success({
                            title: '新增成功！',
                            content: '回到列表...',
                        });
                        this.setState({
                            visible: false,
                        });
                        this.init();
                        this.props.form.resetFields()
                    })
                }
            });
        }
    };
    handleDelete=e=>{
        confirm({
            title: '警告',
            content: '确认删除吗？',
            onOk:()=>{
                axios.delete(`http://cmovie.holyzq.com/api/admin/v1/advertisements/${e.id}`).then(res=>{
                    this.init()
                })
            },
        });
    };
    handleCancel = () => {
        this.setState({
            visible: false,
        });
    };

    columns =()=> [
        {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
            render: text => <Link to="/AdvertisementNode">{text}</Link>,
        },
        {
            title: '名称',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '时间',
            dataIndex: 'updated_at',
            key: 'updated_at',
        },
        {
            title: '排序',
            key: 'sort',
            dataIndex: 'sort',
            render: text => (
                <span>{text}
      </span>
            ),
        },
        {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <span>
                <span onClick={this.showModal.bind(this,text)}>编辑</span>
        <Divider type="vertical" />
                <span onClick={this.handleDelete.bind(this,text)}>删除</span>
      </span>
            ),
        },
    ];
    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <div>
                <Modal
                    title={this.state.put_ok?'编辑广告':'新增广告'}
                    visible={this.state.visible}
                    onOk={this.handleSubmit}
                    onCancel={this.handleCancel}
                    cancelText={'取消'}
                    okText={this.state.put_ok?'修改':'新增'}
                >
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="名称">
                            {getFieldDecorator('name', {
                                rules: [{ required: true, message: '请输入正确的名称' }],
                                initialValue:this.state.dataNews.name
                            })(<Input
                                placeholder='请输入名称'
                            />)}
                        </Form.Item>
                        <Form.Item label="排序">
                            {getFieldDecorator('sort', {
                                rules: [{ required: true, message: '请输入正确的名称' }],
                                initialValue:this.state.dataNews.sort
                            })(<Input
                                placeholder='请输入排序'
                            />)}
                        </Form.Item>
                        <Form.Item label="广告序号">
                            {getFieldDecorator('advertisement_node_id', {
                                rules: [{ required: true, message: '请输入正确的名称' }],
                                initialValue:this.state.dataNews.advertisement_node_id
                            })(<Input
                                placeholder='请输入序号'
                            />)}
                        </Form.Item>
                        <Form.Item label="照骗id">
                            {getFieldDecorator('photo_id', {
                                rules: [{ required: true, message: '请输入正确的名称' }],
                                initialValue:this.state.dataNews.photo_id
                            })(<Input
                                placeholder='照骗标题'
                            />)}
                        </Form.Item>
                        <Form.Item label="广告网址">
                            {getFieldDecorator('url', {
                                rules: [{ required: true, message: 'Please input your note!' }],
                                initialValue:this.state.dataNews.url
                            })(<Input
                                placeholder='请输入网址'
                            />)}
                        </Form.Item>
                    </Form>
                </Modal>
                <Button type="primary" className="newAd" onClick={this.showModal}>新增广告</Button>
                <Table columns={this.columns()} dataSource={this.state.ad} rowKey="id"/>
            </div>
        );
    }
}

const Home = Form.create({ name: 'coordinated' })(App);
export default Home;