import React from 'react';
import { Route , Switch } from "react-router-dom";


import Notice from './Notice.js';

import Home from '../adViews/Home.js';
import EditAd from '../adViews/EditAd.js'
import CreateAdNode from '../adViews/CreateAdNode.js'
import AdvertisementNode from '../adViews/AdvertisementNode.js'


import MarkHome from '../markViews/Home.js'

let RouterView =()=>{
        return (
            <Switch>
                <Route path="/" exact component={Notice}/>


                <Route path="/AdList" exact component={Home}/>
                <Route path="/AdvertisementNode" exact component={AdvertisementNode}/>
                <Route path="/AdvertisementNode/CreateAdNode" exact component={CreateAdNode}/>
                <Route path="/AdvertisementNode/EditAd/:id" exact component={EditAd}/>

                <Route path="/MarkHome" exact component={MarkHome}/>
            </Switch>
        );
}
export default RouterView;